.plans-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 3rem;
}
.plan h3 {
  margin-bottom: 0.8rem;
}
.plan small {
  margin-bottom: 2rem;
}
.plan h1 {
  color: var(--color-primary-variant);
}
.plan h2 {
  display: inline;
  color: var(--color-grey-400);
}
.plan h4 {
  margin-top: 2rem;
}
.plan p {
  margin-top: 1rem;
  color: var(--color-gray-100);
}
.plan p.disabled {
  color: var(--color-grey-400);
}
.plan a {
  display: block;
  margin-top: 3rem;
  cursor: pointer;
}
/* Media Queries (meduim screens) */
@media (max-width: 1024px) {
  .plans-container {
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }
}
/* Media Queries (small screens) */
@media (max-width: 600px) {
  .plans-container {
    margin-top: -8rem;
    grid-template-columns: 1fr;
  }
  .plan {
    width: 90%;
    margin-inline: auto;
  }
}
