.trainers-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 4rem;
}
.card.trainer {
  padding: 1rem;
}
.trainer-img {
  border-radius: 0 2rem;
  overflow: hidden;
}
.trainer p {
  margin-top: 0.5rem;
}
.trainer h3 {
  margin-top: 0.5rem;
}
.trainer-socials {
  display: flex;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  margin: 2rem 0;
}
.trainer-socials a {
  font-size: 0.7rem;
  background-color: var(--color-gray-600);
  padding: 0.8rem;
  border-radius: 0.7rem;
  transition: var(--transition);
}
.trainer:hover a {
  background-color: var(--color-grey-500);
}
/* Media Queries (meduim screens) */
@media (max-width: 1024px) {
  .trainers-container {
    grid-template-columns: 1fr 1fr;
    gap: 2.5rem;
  }
}
/* Media Queries (small screens) */
@media (max-width: 600px) {
  .trainers-container {
    margin-top: -8rem;
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  .card.trainer {
    width: 90%;
    margin-inline: auto;
  }
}
